* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@mixin dialogBox($backgroundColor, $width) {
  width: $width;
  padding: 10px;
  background: $backgroundColor;
  border: 1px solid black;
  margin: 40px auto;
  position: relative;

  &:after{
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    position: absolute;
    border: 1px solid black;
    left: 50%;
    top: 0;
    transform: translate(-50%, -50%) rotate(45deg);
    background-color: $backgroundColor;
  }
}


body {
  font-family: 'Arial', sans-serif;
  background: white;
}

form {
  margin: 20px auto;
  text-align: center;
}

form input {
  margin: 10px 0;
}

header {
  margin: 20px 0;
}

.main-width {
  width: 800px;
  margin: 0 auto;
  padding: 0 40px;
}

.main-width a {
  float: left;
  display: block;
  font-size: 150%;
  line-height: 50px;
  width: 50%;
}

.main-width ul {
  float: left;
  list-style: none;
  width: 50%;

  &:after {
    content: "";
    display: block;
    clear: both;
  }
}

.main-width {
  li {
    float: left;
    margin-right: 20px;
    font-size: 100%;
    line-height: 50px;
  }

  &:after {
    content: "";
    display: block;
    clear: both;
  }
}


